form {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  display: flex;
}

label {
  color: #333;
  margin-bottom: 8px;
  font-weight: bold;
  display: block;
}

input[type="text"], input[type="submit"] {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s;
}

input[type="submit"] {
  color: #fff;
  text-transform: uppercase;
  background-color: #45e9f5;
  margin-top: 10px;
  font-weight: bold;
}
